import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { AngularFirestore } from 'angularfire2/firestore';
import { HttpClient } from '@angular/common/http';
import * as convert from 'xml-js';

import {
  Modeler,
  OriginalPropertiesProvider,
  PropertiesPanelModule,
  InjectionNames,
  OriginalPaletteProvider,
} from './../../../bpmn-js/bpmn-js';
// import {propertiesProviderModule} from 'bpmn-js-properties-panel/lib/provider/camunda';
import { CustomPropsProvider } from './../../../props-provider/CustomPropsProvider';
import { CustomPaletteProvider } from './../../../props-provider/CustomPaletteProvider';
import { FORM_CREAR, FORM_ABRIR, FORM_LIST_PROCESS } from './gestion-form';

// import swal from 'sweetalert2'

const customModdle = {
  name: 'customModdle',
  uri: 'http://example.com/custom-moddle',
  prefix: 'custom',
  xml: {
    tagAlias: 'lowerCase',
  },
  associations: [],
  types: [
    {
      name: 'ExtUserTask',
      extends: ['bpmn:UserTask'],
      properties: [
        {
          name: 'worklist',
          isAttr: true,
          type: 'String',
        },
      ],
    },
  ],
};

@Component({
  selector: 'ngx-gestion',
  templateUrl: './gestion.component.html',
  styleUrls: ['./gestion.component.scss'],
})
export class GestionComponent implements OnInit {
  public proceso: Observable<any[]>;
  modeler: any;
  procesos: any[];
  // Actual process
  proSelected: any;

  // db reference
  reference: any;
  value: any;

  // Forms
  formCrear: any;
  formAbrir: any;
  formListProcess: any;
  class: any;
  result: any;


  constructor(private db: AngularFirestore, private http: HttpClient) {
    // Forms
    this.formCrear = FORM_CREAR;
    this.formAbrir = FORM_ABRIR;
    this.formListProcess = FORM_LIST_PROCESS;
    this.proceso = this.db.collection('/proceso').valueChanges();
    this.proceso.subscribe(response => {
      this.procesos = <any[]>response[0];
      console.info(this.proceso);
      this.reference = this.db
        .collection('/proceso')
        .doc('fVsmBzBrtYZaFhbi6xQm');
    });
    this.load();
    this.class = {
      bpmn: 'col-10',
      active: false,
      icon: 'fa fa-arrow-right',
    };
  }

  fullScreen() {
    console.info(this.class.active);
    if (!this.class.active) {
      this.class.bpmn = 'col-12';
      this.class.icon = 'fa fa-arrow-left';
      this.class.active = !this.class.active;
    } else {
      this.class.bpmn = 'col-10';
      this.class.icon = 'fa fa-arrow-right';
      this.class.active = !this.class.active;
    }
  }

  ngOnInit(): void {
    this.modeler = new Modeler({
      container: '#canvas',
      width: '100%',
      height: '600px',
      additionalModules: [
        PropertiesPanelModule,
        // Re-use original bpmn-properties-module, see CustomPropsProvider
        {
          [InjectionNames.bpmnPropertiesProvider]: [
            'type',
            OriginalPropertiesProvider.propertiesProvider[1],
          ],
        },
        { [InjectionNames.propertiesProvider]: ['type', CustomPropsProvider] },

        // Re-use original palette, see CustomPaletteProvider
        {
          [InjectionNames.originalPaletteProvider]: [
            'type',
            OriginalPaletteProvider,
          ],
        },
        { [InjectionNames.paletteProvider]: ['type', CustomPaletteProvider] },
      ],
      propertiesPanel: {
        parent: '#properties',
      },
      moddleExtension: {
        custom: customModdle,
      },
    });
    CustomPropsProvider.getEvent$()
      .subscribe(response => {
        console.info(response);
      });

  }

  handleError(err: any) {
    if (err) {
      console.warn('Ups, error: ', err);
    }
  }

  createForm(event) {
    if (event.valid) {
      const url = '/assets/bpmn/initial.bpmn';
      this.http
        .get(url, {
          headers: { observe: 'response' },
          responseType: 'text',
        })
        .subscribe((x: any) => {
          this.procesos[event.data.Proceso.Nombre.trim()] = {
            historia: [{ bpmn: x, relacion: {} }],
            nombre: event.data.Proceso.Nombre,
          };
          this.proSelected = this.procesos[event.data.Proceso.Nombre];
          this.value = event.data.Proceso.Nombre;
          this.modeler.importXML(x, this.handleError);
        }, this.handleError);
    }
    console.info(event);
  }

  openForm(event) {
    console.info(event);
    this.proSelected = this.procesos[event.data.Proceso.Proceso.Id];
    this.value = event.data.Proceso.Proceso.Id;
    this.modeler.importXML(this.proSelected.historia[0].bpmn, this.handleError);
    console.info('Open: ', this.modeler);
  }

  importForm(event) {
    console.info(event);
    this.http
      .get(event.data.Proceso.BPMN.url, {
        headers: { observe: 'response' },
        responseType: 'text',
      })
      .subscribe((x: any) => {
        this.procesos['prueba'] = {
          historia: [{ bpmn: x, relacion: {} }],
          nombre: 'prueba',
        };
        this.proSelected = this.procesos['prueba'];
        this.value = 'prueba';
        this.modeler.importXML(x, this.handleError);
      }, this.handleError);
  }

  load(): void {
    const options = new Array();
    // place options here
    for (const prop in this.procesos) {
      if (this.procesos.hasOwnProperty(prop)) {
        options.push({ Id: prop, Nombre: this.procesos[prop].nombre });
      }
    }
    this.formListProcess.campos[0].opciones = options;
  }

  save(): void {
    this.modeler.saveXML(
      (err: any, xml: any) => (this.proSelected.historia[0].bpmn = xml),
    );
    this.procesos[this.value] = this.proSelected;
    this.reference.update(this.procesos);
    this.result = JSON.parse(convert.xml2json(this.proSelected.historia[0].bpmn, { compact: true, spaces: 4 }));
    console.info(this.result);
  }

}
